// Sposób użycia:
//   @include fotnType($weight, $size, $height, $spacing)
//     gdzie:
//       $weight - wartość font-weight w jednostkach 100-900
//       $size - wartość font-size w px / em / rem
//       $height - wartość line-height w mnożniku
//       $spacing - wartość letter-spacing w jednostkach em
//     Wszystkie zmienne są opcjonalne.
//       Jeśli nie ma potrzeby użyć zmiennej należy wprowadzić wartość null.
//       Jeśli nie używane są ostatnie zmienne można nie wprowadzać wartości.

// Przykłady użycia:
//   == letter-spacing takie samo jak w body / przodku
//     .selektor {
//       @include fontType(700, 32px, 1.5)
//     }

//   == font-weight i line-height takie samo jak w body / przodku
//     .selektor {
//       @include fontType(null, text-large, null, 0.02em)
//     }

//   == wszystkie elementy czcionki wprowadzone jako zmienne
//     .selektor {
//       @include fontType(bold, header-normal, medium, large)
//     }

@import './error';

$fontWeight: (
  'normal': 400,
  'medium': 500,
  'bold': 700,
  'black': 900,
);
@function getWeight($val) {
  @return map-get($fontWeight, $val);
}

$fontSize: (
  'text-little': 12px,
  'text-small': 14px,
  'text-normal': 16px,
  'text-medium': 18px,
  'text-big': 20px,
  'text-height': 24px,
  'text-large': 28px,
  'header-normal': 32px,
  'header-medium': 36px,
  'header-big': 40px,
  'header-height': 48px,
  'header-large': 60px,
);
@function getSize($val) {
  @return map-get($fontSize, $val);
}

$fontHeight: (
  'little': 1.13,
  'small': 1.25,
  'normal': 1.5,
  'medium': 1.75,
  'big': 2,
  'height': 2.5,
  'large': 3,
);
@function getHeight($val) {
  @return map-get($fontHeight, $val);
}

$fontSpacing: (
  'small': 0.02em,
  'normal': 0.03em,
  'medium': 0.04em,
  'big': 0.05em,
  'large': 0.06em,
);
@function getSpacing($val) {
  @return map-get($fontSpacing, $val);
}

@mixin type($weight: null, $size: null, $height: null, $spacing: null) {
  $fontWeights: (100, 200, 300, 400, 500, 600, 700, 800, 900);
  @if type-of($weight) == 'number' and index('', unit($weight)) {
    @if index($fontWeights, $weight) {
      font-weight: $weight;
    } @else {
      @include error('errText');
    }
  } @else {
    @if #{getWeight($weight)} != '' {
      font-weight: #{getWeight($weight)};
    } @else if $weight == null {
    } @else {
      @include error('errText');
    }
  }

  @if type-of($size) == 'number' and index('px' 'em' 'rem', unit($size)) {
    font-size: #{$size};
  } @else if #{getSize($size)} != '' {
    font-size: getSize($size);
  } @else if $size == null {
  } @else {
    @include error('errText');
  }

  @if type-of($height) == 'number' and index('', unit($height)) {
    line-height: $height;
  } @else if #{getHeight($height)} != '' {
    line-height: getHeight($height);
  } @else if $height == null {
  } @else {
    @include error('errText');
  }

  @if type-of($spacing) == 'number' and index('em', unit($spacing)) {
    letter-spacing: $spacing;
  } @else if #{getSpacing($spacing)} != '' {
    letter-spacing: getSpacing($spacing);
  } @else if $spacing == null {
  } @else {
    @include error('errText');
  }
}

@mixin fulltype(
  $weight: null,
  $size: null,
  $height: null,
  $spacing: null,
  $mediaMin: null,
  $mediaMax: null,
  $size2: null,
  $height2: null
) {
  @if $weight != null {
    $fontWeights: (100, 200, 300, 400, 500, 600, 700, 800, 900);
    @if type-of($weight) == 'number' and index('', unit($weight)) {
      @if index($fontWeights, $weight) {
        font-weight: $weight;
      } @else {
        @include error('errText');
      }
    } @else {
      @if #{getWeight($weight)} != '' {
        font-weight: #{getWeight($weight)};
      } @else {
        @include error('errText');
      }
    }
  }

  $font-size: 0px;
  @if $size != null {
    $sizeMin: 0px;
    @if type-of($size) == 'number' and index('px' 'em' 'rem', unit($size)) {
      $sizeMin: $size;
    } @else {
      @if #{getSize($size)} != '' {
        $sizeMin: #{getSize($size)};
      }
    }

    $sizeMax: 0px;
    @if $size2 != null {
      @if type-of($size2) == 'number' and index('px' 'em' 'rem', unit($size2)) {
        $sizeMax: $size2;
      } @else {
        @if #{getSize($size2)} != '' {
          $sizeMax: #{getSize($size2)};
        }
      }
    }

    @if $sizeMin != 0px {
      @if $size2 == null {
        font-size: $sizeMin;
      } @else if $sizeMax != 0px {
        $font-size: calc(
          $sizeMin + (($sizeMax - $sizeMin) / 1px) * ((100vw - $mediaMin) / (($mediaMax - $mediaMin) / 1px))
        );
        font-size: $font-size;
      } @else {
        @include error('errText');
      }
    } @else {
      @include error('errText');
    }
  }

  @if $height != null {
    $heightMin: 0;
    @if type-of($height) == 'number' and index('', unit($height)) {
      $heightMin: $height;
    } @else if #{getHeight($height)} != '' {
      $heightMin: getHeight($height);
    }

    $heightMax: 0;
    @if type-of($height2) == 'number' and index('', unit($height2)) {
      $heightMax: $height2;
    } @else if #{getHeight($height2)} != '' {
      $heightMax: getHeight($height2);
    }

    @if $heightMin != 0 {
      @if $height2 == null {
        line-height: $heightMin;
      } @else if $heightMax !=0 {
        line-height: calc
          ($heightMin - ($heightMax - $heightMin) * ((100vw - $mediaMin) / (($mediaMax - $mediaMin) / 1px)));
      } @else {
        @include error('errText');
      }
    } @else {
      @include error('errText');
    }
  }

  @if type-of($spacing) == 'number' and index('em', unit($spacing)) {
    letter-spacing: $spacing;
  } @else if #{getSpacing($spacing)} != '' {
    letter-spacing: getSpacing($spacing);
  } @else if $spacing == null {
  } @else {
    @include error('errText');
  }
}
